// Home.js
import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import Contact from './Contact';
import './home.css';
import './font.css'
import Navbar from './Navbar';
import Employees from './Employees'; // Import the Employees component
import configData from "./config.json"

const GSheetReader = require('g-sheets-api');

function Home() {


  const [activeSlide, setActiveSlide] = useState(0);
  const [showModal, setShowModal] = useState(true);
  const [homeTitleText, setHomeTitleText] = useState(''); // Google Sheets
  const [h1, setH1] = useState('');
  const [p1, setP1] = useState('');

  const h1test = configData.h1test

  const slides = [
    { src: '/images/image1.jpg', alt: 'Slide 1' },
    { src: '/images/image2.jpg', alt: 'Slide 2' },
  ];

  useEffect(() => {
    const interval = setInterval(() => {
      setActiveSlide((prevSlide) => (prevSlide + 1) % slides.length);
    }, 3000);
    return () => clearInterval(interval);
  }, [slides.length]);

  // Function to open the modal popup
  const openModal = () => {
    setShowModal(true);
  };

  // Function to close the modal popup
  const closeModal = () => {
    setShowModal(false);
  };

  // read google sheet data

  function getValueByKey(results, key) {
    const item = results.find(entry => entry.key === key);
    return item ? item.Value : ""; //  Value null
  }

  useEffect(() => {
    const options = {
      apiKey: 'AIzaSyAKMN26phfEvXQKd9o-za_hpzHlptGzBFA',
      sheetId: '1cG4Tlm1YgdLCZKuivkSyy8pOcx-YzEZ5Trxh8GYvhDY',
      sheetName: "1",
    };

    GSheetReader(options, results => {
      setHomeTitleText(getValueByKey(results, "HomeTitleText"));
      setH1(getValueByKey(results, "h1"));
      setP1(getValueByKey(results, "p1"));
    });

    // google link 
    const linkPreconnect1 = document.createElement('link');
    linkPreconnect1.rel = 'preconnect';
    linkPreconnect1.href = 'https://fonts.googleapis.com';

    const linkPreconnect2 = document.createElement('link');
    linkPreconnect2.rel = 'preconnect';
    linkPreconnect2.href = 'https://fonts.gstatic.com';
    linkPreconnect2.crossOrigin = 'anonymous';

    const linkStylesheet = document.createElement('link');
    linkStylesheet.href = 'https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap';
    linkStylesheet.rel = 'stylesheet';

    //  head 
    document.head.appendChild(linkPreconnect1);
    document.head.appendChild(linkPreconnect2);
    document.head.appendChild(linkStylesheet);

    // 
    return () => {
      document.head.removeChild(linkPreconnect1);
      document.head.removeChild(linkPreconnect2);
      document.head.removeChild(linkStylesheet);
    };
  }, []);

  return (
    <div className=' merriweather-light'>
      <div className='backgroundimage'>
        <Navbar />
        <div className='HomeTitleText merriweather-light'>
          {/* <h1>This is The backgroundimage TEXT.</h1> */}
          <h1>{h1test}</h1>
          <p>One Barbershops</p>
        </div>
      </div>

      <div class="box">
        <div class="imagebox">
          <img src="https://images.squarespace-cdn.com/content/v1/65258dc24269c97c749cab95/b8b21e40-594b-47ca-80bc-8b5f2843d6e2/Untitled+design+-+2023-10-10T152515.310.png" alt="This is img"></img>
        </div>
        <div class="textbox merriweather-light" >
          <h1>Where Tradition <br></br>Meets Precision</h1>
          <p>Welcome to Campus Barbers, the iconic barbershop nestled right in the heart of East Lansing, Michigan, on the vibrant campus of Michigan State University. For over seven decades, quality service has been our unwavering commitment. Our barbers are not just professionals; they are experienced veterans in the world of barbering. With decades of expertise, they possess a keen understanding of the art and science of haircuts, ensuring that every client who walks through our doors leaves with a hairstyle that boosts their confidence and suits their individual style.</p>
        </div>
      </div>

      <main>
        {/* Button to Open the Popup */}
        <button onClick={openModal} className="popup-button">Show Discount Info</button>

        {/* Modal Popup */}
        {showModal && (
          <div className="modal">
            <div className="modal-content">
              <span className="close-button" onClick={closeModal}>&times;</span>
              <img src="/images/discount.jpg" alt="Discount Information" style={{ width: '100%' }} />
            </div>
          </div>
        )}
        <div>
          <h1>Welcome to My Barbershop</h1>
          <p>Book your appointment or visit us at our location!</p>
          <div className="carousel">
            <div className="slideshow-container">
              {slides.map((slide, index) => (
                <div key={index} className={`slide ${index === activeSlide ? 'active' : ''}`}>
                  <img src={slide.src} alt={slide.alt} />
                </div>
              ))}
            </div>
          </div>
          <div className="map-container" style={{ marginTop: '20px' }}>
            <iframe
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d401114.0396748494!2d-82.81865374312787!3d42.466896771294685!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x8824aefed3de8e47%3A0x703051f15ffc32e3!2sOne%20World%20Market!5e1!3m2!1sen!2sus!4v1728192088116!5m2!1sen!2sus"
              width="100%"
              height="450"
              style={{ border: 0 }}
              allowFullScreen=""
              loading="lazy"
              title="Barbershop Location"
            ></iframe>
          </div>
          <Routes>
            <Route path='/contact' element={<Contact />} />
            <Route path='/employees' element={<Employees />} />
          </Routes>

        </div>
      </main>

      <footer>
        <p>&copy; 2024 My Barbershop. All rights reserved.</p>
      </footer>


    </div>

  );
}

export default Home;
