import React, { useState, useEffect } from 'react';
import GSheetReader from 'g-sheets-api';
import './employees.css'; // You can style the page with this CSS file
import Navbar from './Navbar';
function Employees() {
  const [employees, setEmployees] = useState([]);

  // Fetch data from Google Sheets
  useEffect(() => {
    const options = {
      apiKey: 'AIzaSyAKMN26phfEvXQKd9o-za_hpzHlptGzBFA',
      sheetId: '1cG4Tlm1YgdLCZKuivkSyy8pOcx-YzEZ5Trxh8GYvhDY',
      sheetName: "1",  // Make sure the sheet name matches
    };

    GSheetReader(options, results => {
      const employeeData = results.map(entry => ({
        name: entry.key,  // Assuming 'key' contains the employee name
        photoUrl: entry.Value // Assuming 'Value' contains the photo URL
      }));
      setEmployees(employeeData);
    });
  }, []);

  return (
    <div>
        <Navbar />
        <div className="employees-container">
        <h1>Our Barbers</h1>
        <div className="employees-grid">
            {employees.map((employee, index) => (
            <div key={index} className="employee-card">
                <img src={employee.photoUrl} alt={employee.name} className="employee-photo" />
                <h2>{employee.name}</h2>
            </div>
            ))}
        </div>
        </div>
    </div>
  );
}

export default Employees;
